import React from 'react'
import { FlagsCounter } from "../functions/metrics"

export let MineCounter = ({matrix, mines})=>{
  let mineCounter = FlagsCounter(matrix, mines)
  return(
    <div className="flagCount">
                    {mineCounter.map((x,i)=>{return( <div key={i} className={"num-"+ x}></div>)})}
                    </div>
  )
}