import { useEffect, useState } from 'react'
import React from 'react'

export let Timer = ({ counterArr, setTick, isPlay }) => {
  let [intervalId, setIntervalID] = useState()
  useEffect(() => {
    if (isPlay) {
      let test = setInterval(() => {
        setTick()
      }, 1000)
      setIntervalID(test)
    } else {
      clearInterval(intervalId)
    }
  }, [isPlay])

  let timerArr = counterArr.map((x, key) => {
    return <div className={`num-${x}`} key={key}></div>
  })
  return <div className="timer">{timerArr}</div>
}
