export let FlagsCounter = (matrix, mines) => {
  let flags = 0
  for (let y = 0; y < matrix.length; y++) {
    for (let x = 0; x < matrix[y].length; x++) {
      if(matrix[y][x].isFlag)flags+=1
    }
  }
  let flagsCount = mines - flags
  let flagsParseToArray = []
  flagsCount = flagsCount.toString().split('')
  if (flagsCount.length === 1) {
    flagsParseToArray = ['0', '0', ...flagsCount]
  } else if (flagsCount.length === 2) {
    flagsParseToArray = ['0', ...flagsCount]
  } else if (flagsCount.length === 3) {
    flagsParseToArray = [...flagsCount]
  }
  for (let x = 0; x < flagsParseToArray.length; x++) {
    if (flagsParseToArray[x] === '-') {
      flagsParseToArray[x] = 'minus'
    }
  }

  return flagsParseToArray
}

export let updateTimerArr = (arr) => {
  if (arr[0] === 9 && arr[1] === 9 && arr[2] === 9) {
    return arr
  }
  for (let i = arr.length - 1; i >= 0; i--) {
    if (arr[i] < 9) {
      arr[i] += 1
      return arr
    } else if (arr[i] === 9) {
      arr[i] = 0
      continue
    }
  }
}

