import React from 'react'
import { connect } from 'react-redux'
import { createMatrix } from '../functions/createMatrix'
import { Field } from './Field'
import { setNewMatrix, setKeyPressInCell, setTick, setRestart, setLevel} from '../store/mainReducer'
import {
  mouseDownHandler,
  mouseUpHandler,
  mouseLeaveHandler,
} from '../functions/handlers'

class FieldContainer extends React.Component {
  constructor(props) {
    super(props)
    this.splitHandlers = (event, id) => {
      switch (event.type) {
        case 'mousedown':
          let obj = mouseDownHandler(event, id)
          if (obj) this.props.setKeyPressInCell(obj)
          break
        case 'mouseup':
          this.props.setKeyPressInCell(mouseUpHandler(event, id))
          break
        case 'mouseleave':
          mouseLeaveHandler()
          break
        default:
          return
      }
    }
  }
  componentDidMount() {
    let matrix = createMatrix(this.props.size, this.props.size)
    this.props.setNewMatrix(matrix)
  }

  classes(cell) {
    if (cell.envBackLight) {
      return 'backLight'
    } else if (!cell.show && cell.isFlag) {
      return 'flag'
    } else if (!cell.show) {
      return 'hidden'
    } else if (cell.show) {
      if (cell.isMine) {
        return 'mine'
      } else if (cell.mineCounter) {
        return 'n' + cell.mineCounter
      } else {
        return 'clear'
      }
    }
  }

  render() {
    return (
      <Field
        matrix={this.props.matrix}
        classes={this.classes}
        splitHandlers={this.splitHandlers}
        counterArr={this.props.counterArr}
        isPlay={this.props.isPlay}
        setTick={this.props.setTick}
        buttonStatus={this.props.buttonStatus}
        setRestart={this.props.setRestart}
        setLevel={this.props.setLevel}
        mines={this.props.mines}
      />
    )
  }
}
const mapStateToProps = (state) => ({
  size:state.main.size,
  mines:state.main.mines,
  matrix: state.main.matrix,
  counterArr: state.main.counterArr, 
  buttonStatus: state.main.buttonStatus,
  isPlay:state.main.isPlay
})

export default connect(mapStateToProps, { setNewMatrix, setKeyPressInCell, setTick, setRestart, setLevel})(
  FieldContainer
)
