import { setRandomMine } from './createMatrix'
import {
  forEach,
  openingClearCells,
  getCellById,
  getAroundCells,
} from './helpers'
//import { setRandomMine } from './createMatrix'

export let leftHandler = (matrix, id, isPlay, mines) => {
  if (!isPlay) {
    setRandomMine(matrix, mines, id)
  }
  let cell = getCellById(matrix, id)
  cell.envBackLight = false
  if (!cell.isFlag) {
    cell.show = true
    if (cell.isMine) {
      forEach(matrix, (x) => (x.show = true))
    } else if (!cell.mineCounter) {
      openingClearCells(matrix, cell)
    }
  }
  return matrix
}

export let rightHandler = (matrix, id) => {
  let cell = getCellById(matrix, id)
  if (!cell.show) {
    cell.isFlag = !cell.isFlag
  }
  return matrix
}

export let leaveHandler = (matrix) => {
  forEach(matrix, (x) => (x.envBackLight = false))
  return matrix
}

export let bothDown = (matrix, id) => {
  let cell = getCellById(matrix, id)
  if (cell.isFlag || !cell.mineCounter || !cell.show) {
    return matrix
  }
  let envCells = getAroundCells(matrix, cell.y, cell.x)
  envCells
    .filter((x) => !x.show && !x.isFlag)
    .map((x) => (x.envBackLight = true))
  return matrix
}
export let bothUp = (matrix, id) => {
  let cell = getCellById(matrix, id)
  forEach(matrix, (x) => (x.envBackLight = false))
  let envCells = getAroundCells(matrix, cell.y, cell.x)
  let flags = envCells.filter((x) => x.isFlag).length
  let mines = envCells.filter((x) => x.isMine).length
  if (flags !== mines) {
    return matrix
  } else if (flags === mines && flags + mines !== 0) {
    envCells.map((x) => {
      if (x.isMine && !x.isFlag) {
        forEach(matrix, (x) => (x.show = true))
      } else if (!x.isMine && !x.mineCounter) {
        openingClearCells(matrix, x)
      } else if (!x.isMine) {
        x.show = true
      }
      return matrix
    })
  }
  return matrix
}

export const pressLeft = (matrix, id) => {
  let cell = getCellById(matrix, id)
  if (!cell.show || cell.isFlag) cell.envBackLight = true
  return matrix
}

export const setButtonClass = (matrix) => {
  let flatMatrix = []
  for (let y = 0; y < matrix.length; y++) {
    for (let x = 0; x < matrix[y].length; x++) {
      if (matrix[y][x].isMine && matrix[y][x].show) {
        return { buttonStatus: 'lose', isPlay: false }
      }
      flatMatrix.push(matrix[y][x])
    }
  }
  if (flatMatrix.every((x) => x.show || x.isFlag)) {
    return { buttonStatus: 'win', isPlay: false }
  }
  return { buttonStatus: 'play', isPlay: true }
}
