import { createMatrix } from '../functions/createMatrix'
import { updateTimerArr } from '../functions/metrics'
import {
  leaveHandler,
  leftHandler,
  rightHandler,
  bothDown,
  bothUp,
  pressLeft,
  setButtonClass,
  timerStatus,
} from '../functions/updateMatrix'

const initialState = {
  matrix: [],
  isPlay: false,
  counterArr: [0, 0, 0],
  buttonStatus: 'play',
  size:9,
  mines:10
}

export const mainReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'NEWMATRIX':
      return {
        ...state,
        matrix: [...action.matrix],
      }
    case 'bothDown':
      return {
        ...state,
        matrix: [...bothDown(state.matrix, action.id)],
      }
    case 'bothUp':
      return {
        ...state,
        matrix: [...bothUp(state.matrix, action.id)],
        ...setButtonClass(state.matrix),
      }
    case 'pressLeft':
      return {
        ...state,
        matrix: [...pressLeft(state.matrix, action.id)],
      }
    case 'left':
      return {
        ...state,
        matrix: [...leftHandler(state.matrix, action.id, state.isPlay, state.mines)],
        ...setButtonClass(state.matrix),
      }
    case 'right':
      return {
        ...state,
        matrix: [...rightHandler(state.matrix, action.id)],
        ...setButtonClass(state.matrix),
      }
    case 'leave':
      return {
        ...state,
        matrix: [...leaveHandler(state.matrix)],
      }
    case 'tick':
      return {
        ...state,
        counterArr: [...updateTimerArr(state.counterArr)],
      }
    case 'restart':
      return {
        ...initialState,
        matrix: [...createMatrix(state.size, state.size)],
        counterArr: [0, 0, 0],
        size:state.size,
        mines:state.mines
      }
    case 'setLevel':
      return{
        ...initialState, size:action.size, mines:action.mines, matrix:[...createMatrix(action.size, action.size)]
      }
    default:
      return state
  }
}

export const setNewMatrix = (matrix) => {
  return { type: 'NEWMATRIX', matrix }
}
export const setKeyPressInCell = ({ type, id }) => {
  return { type, id }
}
export const setTick = () => {
  return { type: 'tick' }
}
export const setRestart = () => {
  return { type: 'restart' }
}
export const setLevel = ({size, mines}) => {
  return { type: 'setLevel', size, mines }
}
