export let getCell = (matrix, y, x) => {
  //находит клетку в матрице
  if (!matrix[y] || !matrix[y][x]) {
    return false
  }
  return matrix[y][x]
}

export let getAroundCells = (matrix, y, x) => {
  //находит все клетки что окружают исходную клетку
  let cells = []
  for (let dy = -1; dy <= 1; dy++) {
    for (let dx = -1; dx <= 1; dx++) {
      if (dx === 0 && dy === 0) {
        continue
      }
      const cell = getCell(matrix, y + dy, x + dx)
      if (cell) {
        cells.push(cell)
      }
    }
  }
  return cells
}

export let forEach = (matrix, handler) => {
  // функция для перебора матрицы и применения к каждому элементу функции из параметра
  for (let y = 0; y < matrix.length; y++) {
    for (let x = 0; x < matrix[y].length; x++) {
      handler(matrix[y][x])
    }
  }
}

/* ========================================= */

export let openingClearCells = (matrix, cell) => {
  //открыть все свободные клетки
  let freeCells = [cell] //массив для свободных клеток
  let numCells = [] //массив для сбора клеток содержащих число (отдельно чтобы в цикле не искать их окружение)
  let flag = true //условие для цикла
  let cycleCounter = 0 //исходная точка для начала верхнего цикла

  while (flag) {
    flag = false
    let cells = [] //массив для сбора свободных клеток в цикле
    for (let a = cycleCounter; a < freeCells.length; a++) {
      //итерации по массиву со свободными клетками
      let aroundcells = getAroundCells(matrix, freeCells[a].y, freeCells[a].x)
      for (let c of aroundcells) {
        if (
          !c.isFlag &&
          !c.isMine &&
          !c.show &&
          !c.mineCounter  &&
          !freeCells.some(x => x.id === c.id) &&
          !cells.some(x => x.id === c.id) 
        ) {
          cells.push(c)
        } else if (c.mineCounter && !numCells.some(x => x.id === c.id)) {
          numCells.push(c)
        }
      }
    }
    cycleCounter++
    if (cells.length) {
      freeCells = [...freeCells, ...cells]
      flag = true
    } else {
      flag = false
    }
  } //end while
  console.log(freeCells)
  freeCells = [...freeCells, ...numCells]
  freeCells.map((x) => (x.show = true))
  return matrix
}

export let getCellById = (matrix, cell) => {
  for (let i = 0; i < matrix.length; i++) {
    for (let j = 0; j < matrix[i].length; j++) {
      if (matrix[i][j].id === Number(cell)) {
        return matrix[i][j]
      }
    }
  }
  return false
}
