export let eventInfo = (e) => {
  if (e.nativeEvent.which === 1) pressButtons.left = !pressButtons.left
  if (e.nativeEvent.which === 3) pressButtons.right = !pressButtons.right
}
let pressButtons = {
  left: false,
  right: false,
}
let downId = null

export let mouseDownHandler = (e, id) => {
  eventInfo(e)
  downId = id
  if (pressButtons.left && pressButtons.right) {
    return { type: 'bothDown', id }
  }else if(pressButtons.left){
    return { type: 'pressLeft', id }
  }
}

export let mouseUpHandler = (e, id) => {
  const both =
    pressButtons.left &&
    pressButtons.right &&
    (e.nativeEvent.which === 1 || e.nativeEvent.which === 3)
  const leftMouse = pressButtons.left && e.nativeEvent.which === 1 && !both
  const rightMouse = pressButtons.right && e.nativeEvent.which === 3 && !both
  eventInfo(e)
  if (id !== downId) {
    return { type: 'leave', id: '' }
  }
  if (both) {
    return { type: 'bothUp', id }
  }

  if (leftMouse) {
    return { type: 'left', id }
  } else if (rightMouse) {
    return { type: 'right', id }
  }
}

export let mouseLeaveHandler = (event) => {
  pressButtons.left = false
  pressButtons.right = false
}
