import React from 'react'

export const Settings = ({setLevel})=>{
  return (
    <div className="menu">  
      <ul className="menuList">
        <li onClick={()=>{setLevel({size:9, mines:10})}}>новичок</li>
        <li onClick={()=>{setLevel({size:16, mines:40})}}>любитель</li>
        <li onClick={()=>{setLevel({size:30, mines:99})}}>профессионал</li>
      </ul>
    </div>
  )
}
