import React from 'react'
import { MineCounter } from './MineCounter'
import { Timer } from './Timer'
import { Button } from './Button'
import { Settings } from './Settings'

export const Field = ({
  matrix,
  counterArr,
  classes,
  splitHandlers,
  setTick,
  buttonStatus,
  setRestart,
  isPlay,
  setLevel,
  mines,
}) => {
  let keyCounter = 1
  let table = matrix.map((y) => {
    return (
      <tr key={'key' + keyCounter++}>
        {y.map((x) => {
          return (
            <td
              key={x.id}
              id={x.id}
              className={classes(x)}
              onMouseDown={(e) => splitHandlers(e, x.id)}
              onMouseUp={(e) => splitHandlers(e, x.id)}
              onMouseLeave={splitHandlers}
              onContextMenu={(e) => e.preventDefault()}
            ></td>
          )
        })}
      </tr>
    )
  })
  return (
    <>
      <Settings setLevel={setLevel} />
      <table>
        <caption className="header">
          <div className="block">
            <MineCounter matrix={matrix} mines={mines} />
            <Button buttonStatus={buttonStatus} setRestart={setRestart} />
            <Timer counterArr={counterArr} setTick={setTick} isPlay={isPlay} />
          </div>
        </caption>
        <tbody>{table}</tbody>
      </table>
    </>
  )
}
