import {getAroundCells} from './helpers'

export let createMatrix = (axisY, axisX) => { //генерирование матрицы
    let matrix = [];
    let idCounter = 1;
    for (let y = 0; y < axisY; y++) {
      let row = [];
      for (let x = 0; x < axisX; x++) {
        row.push({
          id: idCounter++,
          show: false,
          isMine: false,
          isFlag: false,
          envBackLight:false,
          mineCounter: 0,
          marked:false,
          x,
          y,
        });
      }
      matrix.push(row);
    }
    /* setRandomMine(matrix,10) */
    return matrix;
  };


  export let getRandomFreeCells = (matrix, id) => {
      //возвращает случайнуб свободную клетку (для установки мины setRandomMine)
    const freeCells = [];
    for (let y = 0; y < matrix.length; y++) {
      for (let x = 0; x < matrix[y].length; x++) {
        const cell = matrix[y][x];
        if (!cell.isMine && cell.id!==id) {
          freeCells.push(cell);
        }
      }
    }
    const index = Math.floor(Math.random() * freeCells.length);
    return freeCells[index];
  };
  
  export let setRandomMine = (matrix, mines, id) => {//размещение мин
    for (let i = 1; i <= mines; i++) {
      const cell = getRandomFreeCells(matrix, id);
      cell.isMine = true;
      let aroundcells = getAroundCells(matrix, cell.y, cell.x);
      for (const cell of aroundcells) {
        cell.mineCounter += 1;
      }
    }
  };

